/* ========================================
// Anchors
// ===================================== */

a {
  text-decoration: none;
  color: $m2;
  transition:
    background .2s,
    color .2s;

  &:active {}

  &:hover {
    color: $m1;
  }

}


/* ========================================
// Blockquote / Cite
// ===================================== */

blockquote {
  margin: 0 0 em($push, 20);
  font-weight: 300;
  color: $m2;

  .block-out & {
    font-size: em(20);
    line-height: nu(32, 20);
    color: $m1;

    @mixin media $medium {
      font-size: em(30);
      line-height: nu(48, 30);
    }

  }

  strong {
    font-weight: 600;
  }

}

cite {
  display: block;
  margin-top: em(20, 18);
  font-size: em(18, 20);
  font-style: normal;
  font-weight: 800;

  @mixin media $medium {
    margin-top: em(30, 22);
    font-size: em(22, 30);
  }

  &:before {
    content: '- ';
  }

}


/* ========================================
// Lists
// ===================================== */

ul,
ol {
  margin-top: 0;
  margin-bottom: em($push);
  font-size: em(14);
  line-height: nu(23, 14);

  @mixin media $medium {
    font-size: em($bodySize);
    line-height: nu($bodyLine, $bodySize);
  }

}

.styled-list {
  padding-left: 0;
  list-style: none;

  li {
    position: relative;
    padding-left: rem(20);

    @mixin media $medium {
      padding-left: em(25);
    }

  }

    a {
      color: $m1;

      &:hover {
        color: $m2;
      }

    }

    svg {
      position: absolute;
      top: rem(7);
      left: 0;
      fill: $m3;

      @mixin media $medium {
        top: rem(11);
      }

    }

}

.divided-list {

  li {

    @mixin media $medium {
      display: inline-block;
      vertical-align: top;
      width: 49%;
    }

    @mixin media $gamagori {
      width: 32%;
    }

    @mixin media $penguindrum {
      width: 24%;
    }

  }

}

.condensed-list {

  li {

    &:nth-child(n+5) {
      display: none;

      @mixin media $medium {
        display: inline-block;
      }

    }

  }

}


/* ========================================
// Paragraphs
// ===================================== */

p {
  margin-top: 0;
  margin-bottom: em($push);
  font-size: em(14);
  line-height: nu(23, 14);

  @mixin media $medium {
    font-size: em($bodySize);
    line-height: nu($bodyLine, $bodySize);
  }

}


/* ========================================
// Tables
// ===================================== */

table {
  width: 100%;
  border-spacing: 0;
  margin-bottom: em($push);

  th,
  td {
    border-right: 1px solid rgba(0, 0, 0, .125);
    padding: rem(15) rem($gutter);

    @mixin media $silkDrawerDisappears {
      padding: rem(25) rem(50);
    }

    &:last-child {
      border-right: 0;
    }

  }

  th {
    @mixin h3;
    text-align: left;
    background: $m3;
    color: #fff;
  }

  td {
    font-size: em(14);
    line-height: nu(23, 14);

    @mixin media $medium {
      font-size: em($bodySize);
      line-height: nu($bodyLine, $bodySize);
    }

  }

}

  /* Every td in every odd row */
  table tr:nth-of-type(even) td {
    background: rgba(30, 0, 0, .025);
  }


/* ========================================
// Lead In
// ===================================== */

.lead-in {
  @mixin h6;
  font-style: normal;

  @mixin media $medium {
    font-size: em(22);
    line-height: nu(34, 22);
  }

  &--auto {

    p {

      &:first-child {
        @mixin h6;
        font-style: normal;

        @mixin media $medium {
          font-size: em(22);
          line-height: nu(34, 22);
        }

      }

    }

  }

}


/* ========================================
// Horizontal Rule
// ===================================== */

hr {
  border: 0;
  border-bottom: .5px solid #979797;
  margin: em(50) 0;

  @mixin media $medium {
    margin: em(80) 0;
  }

}
