/* ========================================
// Media
// ===================================== */

/* Figure
// ------------------------------------- */

figure {
  margin: 0;

  img {
    display: block;
    max-width: 100%;
  }

}

.shifted-asset {
  margin-bottom: em(30);

  @mixin media $wooser {
    max-width: em(410);
  }

  &--left {

    @mixin media $wooser {
      float: left;
      margin-right: em(40);
    }

  }

  &--right {

    @mixin media $wooser {
      float: right;
      margin-left: em(40);
    }

  }

  &--outlined {
    border: 1px solid #979797;
    padding: em(16);
    background: #fff;
  }

  img {
    margin: 0 auto;
  }

}

@keyframes blockquote-bubble {

  100% {
    opacity: 1;
    transform: scale(1);
  }

}

  figcaption {
    @mixin h6;
    margin-top: em(10);
    font-style: italic;
  }


/* Image
// ------------------------------------- */

img {
  display: block;
  max-width: 100%;

  .hero > & {
    display: none;
  }

}

.full-width {
  width: 100%;
}

.titled-image {
  margin-bottom: em(35);
}


/* Iframe
// ------------------------------------- */

iframe {
  display: block;
  width: 100%;
}

  .responsive-iframe {
    position: relative;
    height: 0;
    overflow: hidden;
    padding-bottom: 56.25%;
  }

  .responsive-iframe iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }


/* SVG
// ------------------------------------- */

svg {}


/* Video
// ------------------------------------- */

video {}
