/* ========================================
// Pagination
// ===================================== */

.pagination {
  text-align: center;
}

  .pagination__control {
    display: inline-block;
    vertical-align: middle;
    border: 0;
    padding: 0;
    background: none;
  }

  .pagination__key {
    @mixin list-reset;
    display: inline-block;
    vertical-align: top;
    padding: 0 em(20);

    li {
      display: inline-block;
      vertical-align: top;
    }

      a {
        display: block;
        padding: 0 em(5, 20);

        @mixin media $medium {
          padding: 0 em(10, 20);
          font-size: em(20);
        }

        &:hover,
        &.active {
          text-decoration: underline;
        }

      }

  }


/* ========================================
// Filter
// ===================================== */

.filter {

  a {
    text-decoration: underline;
  }

}

  .filter__tools {
    padding: em(20) em(10);
    padding-top: em(40);
    background: $m3;
  }

  .filter__extras {
    border-bottom: 1px solid #DBDBDB;
    margin-bottom: em(60, 14);
    margin-top: em(20, 14);
    font-size: em(14);

    @mixin media $wooser {
      display: flex;
      justify-content: space-between;
    }

  }

    .filter__result {
      display: block;
      margin-bottom: em(10, 14);
      font-size: 1rem;
      color: #7D8073;
    }

    .filter__reset {
      display: block;
      margin-bottom: em(10, 14);

      svg {
        margin-right: em(4, 14);
        fill: currentColor;
      }

    }
