/* ========================================
// Sections
// ===================================== */

main {
  padding-top: em(65);
  background: $m1;

  @mixin media $silkDrawerDisappears {
    padding-top: em(127);
  }

}

  .main-content {
    background: $s1;
  }

    .section {
      padding: em(30);

      @mixin media $medium {
        padding: em(40) em(60);
      }

      &--white {
        background: #fff;
      }

      &--collapse__top {
        padding-top: em(10);
      }

    }

    .bubble {
      padding: em(40) em(30) em(30);

      @mixin media $medium {
        padding-top: em(60);
        padding-bottom: em(70);
      }

      &--big {

        @mixin media $wooser {
          padding-top: em(140);
          padding-bottom: em(120);
        }

      }

      &--white {
        background: #fff;
      }

    }


/* ========================================
// Situational
// ===================================== */

.block-out {
  overflow: hidden;
  width: auto;
}

.alignright {
  float: right;
  margin-left: em(16);
  margin-bottom: em(16);
}

.alignleft {
  float: left;
  margin-right: em(16);
  margin-bottom: em(16);
}

.aligncenter {
  margin-left: auto;
  margin-right: auto;
}
