/* ========================================
// Social Feeds
// ===================================== */

.social-feeds {
  overflow: hidden;
  padding: em(20) em(30) em(35);
  background: #F1F1F1;

  @mixin media $huge {
    padding-top: 0;
    padding-bottom: 0;
  }

  ul {
    @mixin list-reset;
    position: relative;
    max-width: em(400);
    margin: 0 auto;
    text-align: center;

    @mixin media $huge {
      display: flex;
      flex-wrap: auto;
      align-items: center;
      max-width: none;
      margin: 0 em(-20);
    }

    @mixin media $yatterman {
      margin: 0 em(-40);
      text-align: left;
    }

    &:before {

      @mixin media $huge {
        position: absolute;
        top: 50%;
        left: 0;
        height: em(160);
        width: 100%;
        content: '';
        pointer-events: none;
        background: linear-gradient(
          to right,
            transparent 0,
            transparent 33%,
            #979797 33%,
            #979797 calc(33% + 1px),
            transparent calc(33% + 1px),
            transparent 66%,
            #979797 66%,
            #979797 calc(66% + 1px),
            transparent calc(66% + 1px),
            transparent 100%
        );
        transform: translate(0, -50%);
      }

    }

    &:after {

      @mixin media $huge {
        @mixin clear;
      }

    }

  }

    li {
      position: relative;
      border-bottom: 1px solid #979797;
      padding: em(45) em(20);
      color: $m1;

      @mixin media $huge {
        border-bottom: 0;
        float: left;
        width: 33.33%;
      }

      @mixin media $yatterman {
        padding-right: em(40);
        padding-left: em(40);
      }

    }

      p {
        margin-bottom: 0;
        font-size: em(16);
        line-height: nu(22, 16);

        @mixin media $medium {
          font-size: em($bodySize);
          line-height: nu($bodyLine, $bodySize);
        }

        @mixin media $yatterman {
          padding-left: em(110);
        }

      }

      img {
        max-width: 100%;
      }

}

@keyframes social-divider {

  100% {
    opacity: 1;
  }

}

  .social__bubble {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: em(90);
    width: em(90);
    margin-bottom: em(20);
    font-size: 80%;
    border-radius: 100%;

    @mixin media $yatterman {
      position: absolute;
      top: 50%;
      left: em(40);
      font-size: 100%;
      transform: translate(0, -50%);
    }

    &--facebook {
      background: #1F2F54;

      svg {
        height: em(42);
        width: em(23);
      }

    }

    &--twitter {
      background: #5F92CC;

      svg {
        height: em(32);
        width: em(40);
      }

    }

  }

    .social__icon {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);

      svg {
        display: block;
        fill: #fff;
      }

    }


/* ========================================
// Instagram
// ===================================== */

.instagram-feed {
  @mixin list-reset;
}

  .instagram-item {
    position: relative;
    display: block;
  }

    .instagram-item__image {
      display: block;
      max-width: 100%;
    }

    .instagram-item__details {}

      .instagram-item__caption {}


/* ========================================
// Twitter
// ===================================== */

.twitter-feed {
  @mixin list-reset;
}

  .twitter-item {}
