/* ========================================
// Partners
// ===================================== */

.partners {
  @mixin list-reset;
  text-align: center;
  background: #f2f2f2;

  @mixin media $wooser {
    display: flex;
    align-items: center;
  }

  @mixin media $aldnoah {
    justify-content: space-between;
  }

}

  .partners__title {
    padding: em(43) em(30) em(42);
    background: $m3;

    @mixin media $wooser {
      padding-right: em(50);
      padding-left: em(50);
    }

    h2 {
      margin-bottom: 0;
      color: #fff;
    }

  }

  @keyframes partner-title {

    100% {
      opacity: 1;
    }

  }

  .partners__item {
    padding: em(20);

    @mixin media $medium {
      display: inline-block;
      vertical-align: middle;
      padding: em(40) em(20);
    }

    @mixin media $wooser {
      padding: em(20);
    }

    &:nth-of-type(2) {
      padding-top: em(40);
      animation-delay: .1s;

      @mixin media $medium {
        padding-bottom: em(40);
      }

      @mixin media $wooser {
        padding-top: em(20);
        padding-bottom: em(20);
        padding-left: em(50);
      }

      @mixin media $aldnoah {
        padding-left: em(20);
      }

    }

    &:nth-of-type(3) {
      animation-delay: .2s;
    }

    &:nth-of-type(4) {
      padding-bottom: em(40);
      animation-delay: .3s;

      @mixin media $medium {
        padding-top: em(40);
      }

      @mixin media $wooser {
        padding-top: em(20);
        padding-bottom: em(20);
      }

    }

    &:nth-of-type(5) {
      animation-delay: .4s;
    }

    &:nth-of-type(6) {
      animation-delay: .5s;
    }

    &:nth-of-type(n+5) {
      display: none;

      @mixin media $aldnoah {
        display: inline-block;
        vertical-align: middle;
      }

    }

    a {
      display: block;
      transition: transform .2s;

      &:hover {
        transform: scale(1.05);
      }

    }

      img {
        margin: 0 auto;
      }

  }

  @keyframes partner {

    100% {
      opacity: 1;
      transform: scale(1);
    }

  }
