/* ========================================
// Video
// ===================================== */

.video-bubble {
  position: relative;
  overflow: hidden;
  padding: em(60) em(30) em(30);

  @mixin media $medium {
    padding-top: em(110);
    padding-bottom: em(80);
  }

  @mixin media $gamagori {
    padding-top: 8vw;
    padding-bottom: 8vw;
  }

  &:before,
  &:after {
    @mixin cover;
    content: '';
    background: rgba(20, 49, 94, .7);
  }

  &:before {
    z-index: 20;
  }

  &:after {
    z-index: 10;
    mix-blend-mode: hard-light;
  }

}

  .video__details {
    position: relative;
    z-index: 40;
    max-width: em(600);
    color: #fff;

    > * {

      .cssanimations & {

        @mixin media $gamagori {
          opacity: 0;
          transform:
            translate(rem(30), 0)
            scale(.95);
          transform-origin: right center;
        }

      }

      .element-halfway & {

        @mixin media $gamagori {
          animation-name: video-details;
          animation-duration: 1s;
          animation-fill-mode: forwards;
        }

      }

      &:nth-child(2) {
        animation-delay: .1s;
      }

      &:nth-child(3) {
        animation-delay: .2s;
      }

      &:nth-child(4) {
        animation-delay: .3s;
      }

      &:nth-child(5) {
        animation-delay: .4s;
      }

    }

    h1 {
      color: inherit;
    }

  }

  @keyframes video-details {

    100% {
      opacity: 1;
      transform:
        translate(0, 0)
        scale(1);
    }

  }

  .video-cover {
    position: absolute;
    top: 0;
    left: 0;
    display: none !important;
    min-width: 100%;
    width: 100%;
    min-height: 100%;
    height: auto;

    @mixin media $wooser {
      display: block !important;
    }

  }
