/* ========================================
// Headings
// ===================================== */

@define-mixin default-heading {
  margin-top: 0;
}


/* 1:Heading One */

@define-mixin h1 {
  @mixin default-heading;
  margin-bottom: rem(15);
  font-family: $sansSerif2;
  font-size: em($h2Size);
  line-height: nu($h2Line, $h2Size);
  text-transform: uppercase;
  color: $m1;

  @mixin media $medium {
    font-size: em($h1Size);
    line-height: nu($h1Line, $h1Size);
  }

}

h1,
.h1 {
  @mixin h1;

  &.larger {
    font-size: em(36);
    line-height: nu(40, 36);

    @mixin media $medium {
      font-size: em($h1Size);
      line-height: nu($h1Line, $h1Size);
    }

    @mixin media $wooser {
      font-size: em(64);
      line-height: nu(70, 64);
    }

  }

}


/* 2:Heading Two */

@define-mixin h2 {
  @mixin default-heading;
  margin-bottom: rem(15);
  font-family: $sansSerif2;
  font-size: em($h3Size);
  line-height: nu($h3Line, $h3Size);
  text-transform: uppercase;
  color: $m1;

  @mixin media $medium {
    font-size: em($h2Size);
    line-height: nu($h2Line, $h2Size);
  }

}

h2,
.h2 {
  @mixin h2;

  &.stay {
    font-size: em($h2Size);
    line-height: nu($h2Line, $h2Size);
  }

  &.larger {
    font-size: em($h2Size);
    line-height: nu($h2Line, $h2Size);

    @mixin media $wooser {
      font-size: em(30);
      line-height: nu(45, 30);
    }

  }

}


/* 3:Heading Three */

@define-mixin h3 {
  @mixin default-heading;
  margin-bottom: rem(10);
  font-size: em($h4Size);
  font-weight: 800;
  line-height: nu($h4Line, $h4Size);
  text-transform: uppercase;
  color: $m2;

  @mixin media $medium {
    font-size: em($h3Size);
    line-height: nu($h3Line, $h3Size);
  }

}

h3,
.h3 {
  @mixin h3;

  &.mini {
    font-size: em(15);

    @mixin media $medium {
      font-size: em(18);
    }

  }

}

.flag {
  position: relative;
  margin-bottom: em(20);
  text-align: center;

  &:before {
    position: absolute;
    top: 50%;
    left: 0;
    border-bottom: .5px solid #979797;
    width: 100%;
    content: '';
    transform: translate(0, -50%);
  }

  h3 {
    position: relative;
    display: inline-block;
    margin-bottom: 0;
    padding: 0 em(10, $h3Size);
    text-align: center;
    background: #F8F8F8;
    color: #464646;
  }

}


/* 4:Heading Four */

@define-mixin h4 {
  @mixin default-heading;
  font-size: em($h5Size);
  font-weight: 700;
  line-height: nu($h5Line, $h5Size);
  color: $m1;

  @mixin media $medium {
    font-size: em($h4Size);
    line-height: nu($h4Line, $h4Size);
  }

}

h4,
.h4 {
  @mixin h4;
}


/* 5:Heading Five */

@define-mixin h5 {
  @mixin default-heading;
  font-size: em($h6Size);
  font-weight: 700;
  line-height: nu($h6Line, $h6Size);
  color: #7D8073;

  @mixin media $medium {
    font-size: em($h5Size);
    line-height: nu($h5Line, $h5Size);
  }

}

h5,
.h5 {
  @mixin h5;
}


/* 6:Heading Six */

@define-mixin h6 {
  @mixin default-heading;
  font-size: em($h6Size);
  font-style: italic;
  font-weight: normal;
  line-height: nu($h6Line, $h6Size);
  color: $m2;
}

h6,
.h6 {
  @mixin h6;
}

/* Misc */

.push--bottom {
  margin-bottom: em($push);
}

.push--top {
  margin-top: em($push);
}
