/* ========================================
// Forms
// ===================================== */

/* Container
// ------------------------------------- */

form {}

  fieldset {
    border: 0;
    margin-bottom: em($push);
    padding: 0;
  }


/* Label
// ------------------------------------- */

label {
  margin-right: em(20);
}


/* Shared Input/Select/Textarea
// ------------------------------------- */

@define-mixin form-item-container {
  position: relative;
  overflow: hidden;
}

@define-mixin form-item {
  width: 100%;
  border: 0;
  padding: em(17, 16) em(20, 16);
  font-size: em(16);
  line-height: 24px; /* Specify in pixels for browser consistency */
  appearance: none;
  background: #fff;
  border-radius: em(3, 16);
}


.input-field {
  @mixin form-item-container;
}

  .input-hint {
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: em(50);
    background: url(../icons/chevron-down-blue.svg) no-repeat 50% 50%;
    transition: background .2s;

    &--select {
      pointer-events: none;
    }

    &--submit {
      background-image: url(../icons/search-dark-blue.svg);
    }

  }

    .input-hint__field {
      position: absolute;
      top: 0;
      right: 0;
      height: 100%;
      width: 100%;
      border: 0;
      padding: 0;
      background: none;
      color: transparent;
    }


/* Input
// ------------------------------------- */

input {

  &:disabled {}

  &:checked {}

  &[type='text'] {
    @mixin form-item-container;
    @mixin form-item;
  }

  &[type='radio'],
  &[type='checkbox'] {
    height: em(15);
    width: em(15);
    border: 1px solid;
    cursor: pointer;
    appearance: none;
    transition:
      border .2s,
      background .2s;

    &:checked {
      background: currentColor;
    }

  }

  &[type='radio'] {
    border-radius: 100%;
  }

  &[type='range'] {}

  &::-webkit-input-placeholder {
    color: $bodyColor;
  }

  :-moz-placeholder { /* Firefox 18- */
    color: $bodyColor;
  }

  ::-moz-placeholder {  /* Firefox 19+ */
    color: $bodyColor;
  }

  :-ms-input-placeholder {
    color: $bodyColor;
  }

}


/* Textarea
// ------------------------------------- */

textarea {
  @mixin form-item-container;
  @mixin form-item;
}


/* Select
// ------------------------------------- */

select {
  @mixin form-item;
  cursor: pointer;
}


/* ========================================
// Unique Form Elements
// ===================================== */

.site-search {
  position: relative;
  border-top: 1px solid rgba(255, 255, 255, .1);
  padding: em(14) em(20) em(13);

  @mixin media $silkDrawerDisappears {
    float: right;
    border-top: 0;
    padding: 0;
  }

}

	input.site-search__text:focus::-webkit-input-placeholder { color:transparent; }
	input.site-search__text:focus::-moz-placeholder { color:transparent; }


  .site-search__trigger {
    position: relative;
    display: none;
    height: em(50);
    width: em(50);
    cursor: pointer;

    @mixin media $silkDrawerDisappears {
      display: block;
      transition: height .2s;
    }

    &:hover {

      svg {
        fill: $m2;
      }

    }

    /*.scrolling & {

      @mixin media $silkDrawerDisappears {
        height: em(45);
      }

    }*/

    .vertically-center {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }

      svg {
        display: block;
        fill: $m1;
      }

  }

  .site-search__details {
    position: relative;
    transform-origin: right top;
    transition:
      opacity .2s,
      transform .2s;

    @mixin media $silkDrawerDisappears {
      position: absolute;
      top: 100%;
      right: 0;
      opacity: 0;
      transform: scale(.5);
      z-index: 400;
    }

    .event-triggered & {
      opacity: 1;
      transform: scale(1);
    }

    .site-search__text {
      width: 100%;
      padding: em(7, 14) em(10, 14) em(6, 14);
      font-size: em(14);

      @mixin media $silkDrawerDisappears {
        border: em(3, 14) solid $m1;
        width: em(300, 14);
        padding: em(12, 14);
      }

    }

    .site-search__submit {
      position: absolute;
      top: 0;
      right: 0;
      height: 100%;
      border: 0;
      padding: 0 em(10);
      background: url(../icons/search-dark-blue.svg) no-repeat 50% 50%;
      background-size: em(18);
      color: transparent;
      transition: background .3s;

      @mixin media $silkDrawerDisappears {
        padding: 0 em(17, 14);
        font-size: em(14);
        font-weight: 700;
        background: $m1;
        border-radius: 0 em(3, 14) em(3, 14) 0;
        color: #fff;
      }

      &:hover {

        @mixin media $silkDrawerDisappears {
          background: $m2;
        }

      }

    }

  }

.sign-up {
  padding: em(20) em(30) em(70);
  background: #f1f1f1;

  &:after {

    @mixin media $yatterman {
      @mixin clear;
    }

  }

}

  .sign-up__bubble {

    @mixin media $gamagori {
      padding: 0 em(60);
    }

    .cssanimations & {

      @mixin media $gamagori {
        opacity: 0;
        transform: scale(.85);
      }

    }

    .element-halfway & {

      @mixin media $gamagori {
        animation: blockquote-bubble 1s .1s forwards;
      }

    }

    h2 {
      margin-bottom: rem(20);
      font-size: em(22);
      text-align: center;
      text-transform: none;

      @mixin media $tiny {
        font-size: em(25);
      }

      @mixin media $medium {
        font-size: em(40);
      }

      @mixin media $yatterman {
        float: left;
        margin-top: em(12, 40);
        margin-right: em(50, 40);
        margin-bottom: em(12, 40);
      }

    }

  }

    .sign-up__form {
      font-size: 62%;

      @mixin media $medium {
        font-size: 100%;
      }

      @mixin media $yatterman {
        float: left;
        width: calc(100% - em(470));
      }

      &:after {
        @mixin clear;
      }

      input {
        display: block;
        float: left;
      }

      [type="text"] {
        width: calc(100% - em(180, 22));
        padding: em(25, 22) em(40, 22);
        border-radius: em(2, 22) 0 0 em(2, 22);
        font-size: em(22);
        color: #14315E;

        &::placeholder {
          color: #14315E;
        }

      }

      [type="submit"] {
        width: em(180, 30);
        margin: 0;
        padding: em(18, 30) 0 em(17, 30);
        font-size: em(30);
        border-radius: 0 em(2, 30) em(2, 30) 0;
      }

    }
