/* ========================================
// Bulleted Navigation
// ===================================== */

.nav-bulleted {
  margin-bottom: em(25);
  padding-left: 0;

  @mixin media $huge {
    margin-bottom: em(8);
  }

  ul {
    font-size: em(14);
    letter-spacing: em(.7, 14);
    line-height: nu(22, 14);
  }

    li {
      display: inline-block;
      vertical-align: top;

      &:after {
        display: inline-block;
        vertical-align: top;
        margin-right: em(8, 14);
        margin-left: em(8, 14);
        content: '\2022';
      }

      &:last-child {

        &:after {
          display: none;
        }

      }

    }

      a {
        display: inline-block;
        vertical-align: top;

        &:hover {}

      }

}
