/* ========================================
// Grid / Columns
// ===================================== */

.grid {

  &:after {
    @mixin clear;
  }

  &--20 {
    margin: 0 em(-10);
  }

  &--40 {
    margin: 0 em(-20);
  }

  &--90 {
    margin: 0 em(-20);

    @mixin media $silkDrawerDisappears {
      margin: 0 em(-45);
    }

  }

  &--push-bottom {
    margin-bottom: em($push);
  }

}

.flex {
  display: flex;
  flex-wrap: wrap;

  &--centered {
    align-content: center;
    align-items: center;
  }

  &--justified {
    align-items: baseline;
    justify-content: space-between;
  }

  &--start {
    align-items: flex-start;
  }

  &--bordered {
    border-bottom: 1px solid #979797;
    margin-bottom: em(20);
  }

}

  [class^='col-'] {
    float: left;
    width: 100%;

    .grid--20 & {
      margin-bottom: em(20);
      padding: 0 em(10);
    }

    .grid--40 & {
      margin-bottom: em(40);
      padding: 0 em(20);
    }

    .grid--90 & {
      margin-bottom: em(40);
      padding: 0 em(20);

      @mixin media $silkDrawerDisappears {
        padding: 0 em(45);
      }

    }

    p {

      &:last-child {
        margin-bottom: 0;
      }
      
    }

  }

  @media (min-width: $mini) {

    .col-mini {
      create-grid: 2;
    }

  }

  @media (min-width: $tiny) {

    .col-tiny {
      create-grid: 2;
    }

  }

  @media (min-width: $small) {

    .col-small {
      create-grid: 12;
    }

  }

  @media (min-width: $medium) {

    .col-medium {
      create-grid: 12;
    }

  }

