/* ========================================
// Social Navigation
// ===================================== */

.nav-social {

  @mixin media $medium {
    float: right;
  }

  ul {
    margin: 0 em(-10);
    font-size: 85%;

    @mixin media $medium {
      font-size: 100%;
    }

  }

    li {
      display: inline-block;
      vertical-align: top;
      margin: 0 em(10) em(20);

      @mixin media $medium {
        margin-top: em(32);
        margin-bottom: 0;
      }

    }

      a {
        position: relative;
        height: em(32);
        width: em(32);
        background: #fff;
        border-radius: 100%;

        &:before {
          @mixin cover;
          content: '';
          border-radius: inherit;
          box-shadow: 0 0 0 em(2) #fff;
          opacity: 1;
          transform:
            scale(0)
            rotateX(0)
            rotateY(0);
          transition:
            opacity .5s,
            transform .5s;
        }

        &:hover {
          box-shadow: none;

          &:before {
            animation: barcode .5s forwards;
          }

        }

      }

        .bottle {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }

          svg {
            display: block;
            fill: $s3;
          }

}

@keyframes barcode {

  100% {
    opacity: 0;
    transform:
      scale(3)
      rotateX(90deg)
      rotateY(45deg);
  }

}
