.visible-for-screen-readers {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  border: 0;
  margin: -1px;
  padding: 0;
}

/* Loading icon
// ------------------------------------- */

.loading {
  width: 25px;
  height: 25px;
  background-color: $m1;
  margin: em(30) auto;
  animation: sk-rotateplane 1s infinite ease-in-out;
}

@keyframes sk-rotateplane {
  0% {
    transform: perspective(120px) rotateX(0deg) rotateY(0deg);
  } 50% {
    transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg);
  } 100% {
    transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
  }
}

/* No JS
// ------------------------------------- */

.no-js {

  #vue-posts,
  #solutions-builder {
    display: none;
    visibility: hidden;
  }

  noscript {
    display: block;
    border-left: 5px solid #f00;
    padding-left: em(16);
    background: #ffc;
  }

}
