/* ========================================
// Help
// ===================================== */

.help {}

  .help__back-trigger {
    overflow: hidden;
    display: block;
    max-height: 0;
    font-size: em(16);
    font-weight: 700;
    line-height: 1;
    cursor: pointer;
    color: $m3;
    transition:
      overflow .5s,
      max-height .5s,
      margin .5s,
      color .2s;

    &:hover {
      color: $m2;
    }

    .showing-list & {
      margin-bottom: em(4, 16);
      max-height: em(16, 16);
    }

  }

  .help__label {
    color: $m3;
    transition: color .2s;

    @mixin media $wooser {
      margin-bottom: rem(35);
    }

    .cssanimations & {

      @mixin media $gamagori {
        transform:
          translate(0, em(20))
          scale(.95);
        transform-origin: left top;
      }

    }

    .element-halfway & {

      @mixin media $gamagori {
        animation: help 1s forwards;
      }

    }

    .showing-list & {
      color: $m2;
    }

  }

  @keyframes help {

    100% {
      opacity: 1;
      transform:
        translate(0, 0)
        scale(1);
    }

  }

  .help__list {
    position: relative;
    margin-bottom: 0;

    @mixin media $gamagori {
      column-count: 2;
      transform: translateZ(0);
    }

    &:after {
      transition: opacity .2s;

      @mixin media $gamagori {
        position: absolute;
        top: 0;
        left: 50%;
        height: 100%;
        border-right: 1px solid #979797;
        margin-left: em(-30);
        content: '';
        transform: translate(-50%, 0);
      }

      .showing-list & {
        opacity: 0;
      }

    }

    .cssanimations & {

      @mixin media $gamagori {
        transform:
          translate(0, em(20))
          scale(.975);
        transform-origin: left top;
      }

    }

    .element-halfway & {

      @mixin media $gamagori {
        animation: help 1s .2s forwards;
      }

    }

    > li {
      padding-left: 0;
      color: $m1;
      transition: color .2s;

      &:hover {
        color: $m3;
      }

    }

  }

    .help__root {
      position: relative;
      overflow: hidden;
      display: block;
      max-height: em(100);
      margin: em(6, 14) 0;
      cursor: pointer;
      opacity: 1;
      transition:
        max-height 1s,
        margin 1s,
        opacity 1s;

      @mixin media $medium {
        margin: em(9, $bodySize) 0;
        line-height: nu(35, $bodySize);
      }

      @mixin media $gamagori {
        padding-right: em(60);
        transition:
          margin .3s,
          opacity .3s;
      }

      .showing-list & {
        max-height: 0;
        margin: 0;
        opacity: 0;

        @mixin media $gamagori {
          max-height: em(100);
          margin: em(9, $bodySize) 0;
        }

      }

      svg {
        fill: $m2;
      }

      span {
        display: block;
        padding-left: rem(20);

        @mixin media $medium {
          padding-left: em(25);
        }

      }

    }

    .help__nested-list {
      @mixin list-reset;
      overflow: hidden;
      max-height: 0;
      font-size: rem(14);
      line-height: nu(22, 14);
      opacity: 0;
      visibility: hidden;
      transition:
        max-height 1s,
        opacity 1s,
        visibility 1s;

      @mixin media $medium {
        font-size: em($bodySize);
        line-height: nu(35, $bodySize);
      }

      @mixin media $gamagori {
        position: fixed;
        top: 0;
        left: 0;
        z-index: 50;
        width: 100%;
        max-height: inherit;
        column-count: 2;
        transition:
          opacity .3s,
          visibility .3s;
      }

      &:after {

        @mixin media $gamagori {
          position: absolute;
          top: 0;
          left: 50%;
          height: 100%;
          border-right: 1px solid #979797;
          margin-left: em(-30);
          content: '';
          transform: translate(-50%, 0);
        }

      }

      .show-list + & {
        max-height: em(400);
        opacity: 1;
        visibility: visible;

        @mixin media $gamagori {
          max-height: inherit;
        }

      }

      a {
        display: block;
        margin: em(12, 14) 0;

        @mixin media $medium {
          margin: em(9, $bodySize) 0;
        }

        @mixin media $gamagori {
          padding-right: em(60, $bodySize);
        }

      }

        svg {
          fill: $m3;
        }

    }
