/* ========================================
// Site Footer
// ===================================== */

.site__footer {
  position: relative;
  overflow: hidden;
  padding: em(40) em(20);
  text-align: center;
  background: $s3;
  color: #fff;

  @mixin media $huge {
    text-align: left;
  }

  a {
    color: inherit;
    transition: box-shadow .3s;

    &:hover {
      box-shadow: 0 em(2) 0 0 #fff;
    }

  }

}

  .lace {
    border-bottom: 1px solid #979797;
    margin-bottom: em(35);
    padding-bottom: em(25);

    @mixin media $medium {
      margin-bottom: em(30);
      padding-bottom: em(30);
    }

  }

  .sole {}

    .tread {

      &:first-child {

        @mixin media $huge {
          float: left;
        }

      }

      &:last-child {

        @mixin media $huge {
          float: right;
          text-align: right;
        }

      }

    }
