/* ========================================
// Tier Navigation
// ===================================== */

.nav-tier-bubble {

  @mixin media $large {
    min-height: em(20);
  }

}

.nav-tier-container {

  @mixin media $large {
    display: none;
  }

}

  .nav-tier-switch {
    width: 100%;
    border: 0;
    padding: em(5) em(14);
    text-align: left;
    background: $m1;
    color: #fff;

    span {
      @mixin h3;
      color: #fff;
    }

    svg {
      margin: em(10) 0;
      float: right;
      fill: $m3;
      transition: transform .5s;

      .event-triggered & {
        transform: scaleY(-1);
      }

    }

  }

  .nav-tier {
    display: none;

    @media (min-width: $large) and (min-height: $big) {
      position: fixed;
      top: em(65);
      left: 0;
      width: 33.33333%;
      transition:
        top .2s,
        padding .2s;
    }

    @media (min-width: $wooser) and (min-height: $big) {
      width: 25%;
    }

    @media (min-width: $silkDrawerDisappears) and (min-height: $big) {
      top: em(127);
    }

    @mixin media $large {
      display: block;
      max-height: 100%;
      margin-bottom: 0;
      padding: em(30) em(20);
      background: none;
    }

    &--clone {
      display: block;
      overflow: hidden;
      max-height: 0;
      margin-bottom: em(30);
      padding: 0 em(14);
      background: $m1;
      transition:
        max-height .5s,
        padding .5s;

      .event-triggered & {
        max-height: em(10000);
        padding: em(14);
      }

    }

    > ul {

      > li {

        &:first-child {

          a {
            padding: 0;
            margin-bottom: em(25, 16);
            font-weight: 700;
            color: $m3;

            &:hover {
              color: #fff;

              svg {
                fill: #fff;
              }

            }

          }

        }
      }
    }

    ul {
      font-size: em(14,16);

      &.children {
        margin-left: em(16,14);
        margin-top: em(8, 14);
      }

    }

      li {
        margin-bottom: em(8, 16);

        &.current_page_item,
        &.current_page_parent {

          > a {
            color: $m3;
            font-weight: 700;
          }

        }

        li {

          &.current_page_item,
          &.current_page_parent {

            > a {
              font-weight: 400;
            }

          }

        }

      }

        a {
          font-size: em(16);
          color: #fff;

          &:hover {
            color: $m3;
          }

        }

          svg {
            margin-right: em(10, 16);
            fill: $m3;
          }

  }
