/* ========================================
// Quick Navigation
// ===================================== */

.nav-quick {

  @mixin media $silkDrawerDisappears {
    display: none;
  }

  &--clone {
    display: none;

    @mixin media $silkDrawerDisappears {
      display: block;
      text-align: right;
      background: $m1;
      padding-right: em(12);
    }

  }

  ul {}

    li {

      @mixin media $silkDrawerDisappears {
        display: inline-block;
        vertical-align: top;
      }

    }

      a {
        padding: em(8, 14) em(20, 14);
        font-size: em(14);
        font-weight: 800;
        text-transform: uppercase;
        color: #fff;

        @mixin media $silkDrawerDisappears {
          padding: em(6, 14) em(12, 14);
          transition:
            padding .2s,
            font-size .2s,
            background .2s;
        }

        &:hover {

          @mixin media $silkDrawerDisappears {
            background: $m2;
          }

          svg {
            fill: #fff;
          }

        }

        /*.scrolling & {

          @mixin media $silkDrawerDisappears {
            padding-top: em(5, 12);
            padding-bottom: em(5, 12);
            font-size: em(12);
          }

        }*/

      }

        svg {
          display: inline-block;
          vertical-align: middle;
          margin-right: em(5, 14);
          fill: $m3;
        }

}
