/* ========================================
// Drawer Mixin
// ===================================== */

@define-mixin drawer $transform-intensity: 20% {
  position: absolute;
  min-height: 100%;
  width: 100%;
  pointer-events: none;
  opacity: 0;
  visibility: hidden;
  transform: translate(0, 0);
  transform: translate3d(0, 0, 0);
}

@define-mixin visible-drawer {
  pointer-events: auto;
  opacity: 1;
  visibility: visible;
  transform: translate(0, 0);
  transform: translate3d(0, 0, 0);
}

@define-mixin reset-drawer {
  @mixin visible-drawer;
  position: static;
  overflow: inherit;
  max-width: 100%;
  background: none;
}


/* ========================================
// Drawer
// ===================================== */

.drawer {
  @mixin drawer;
  top: 0;
  right: 0;
  z-index: 200;
  overflow: hidden;
  overflow-y: auto;
  max-width: em(500);
  padding: em(65) 0 em(20);
  background: $m1;
  transition:
    opacity .3s,
    visibility .3s;

  @mixin media $silkDrawerDisappears {
    @mixin reset-drawer;
    float: right;
    width: 68%;
    width: calc(100% - em(270));
    padding-top: em(11);
    padding-bottom: em(11);
    text-align: right;
    transition: padding .2s;
  }

  /*.scrolling & {

    @mixin media $silkDrawerDisappears {
      padding-top: 0;
      padding-bottom: 0;
    }

  }*/

  .visible-drawer & {
    @mixin visible-drawer;

    @mixin media $silkDrawerDisappears {
      @mixin reset-drawer;
    }

  }

}


/* ========================================
// Drawer Trigger
// ===================================== */

.drawer__trigger {
  position: absolute;
  top: 50%;
  right: em(20, 14);
  z-index: 300;
  border: 0;
  padding: em(3, 14) 0;
  outline: 0;
  cursor: pointer;
  font-size: em(14);
  font-weight: 700;
  text-transform: uppercase;
  background: none;
  color: $m1;
  transform: translate(0, -50%);
  transition: color .4s;

  @mixin media $silkDrawerDisappears {
    display: none;
  }

  &:before,
  &:after {
    position: absolute;
    left: 50%;
    height: em(3, 14);
    width: em(45, 14);
    content: '';
    background: currentColor;
    color: #000;
    transform: translate(-50%, 0);
    transition:
      top .4s,
      bottom .4s,
      height .4s,
      width .4s,
      margin .4s,
      background .4s,
      color .4s,
      box-shadow .4s,
      transform .4s;

    .visible-drawer & {
      height: em(2, 14);
      width: em(26, 14);
      color: #fff;
    }

  }

  &:before {
    top: 0;

    .visible-drawer & {
      top: 50%;
      transform:
        translate(-50%, -50%)
        rotate(-45deg);
    }

  }

  &:after {
    bottom: 0;

    .visible-drawer & {
      bottom: 50%;
      transform:
        translate(-50%, 50%)
        rotate(45deg);
    }

  }

}
