/* ========================================
// Main Navigation
// ===================================== */

@define-mixin tier-one {
  border-top: 1px solid rgba(255, 255, 255, .1);
  padding: em(13, 14) em(20, 14);
  font-size: em(14);
  font-weight: 700;
  text-transform: uppercase;
  color: #fff;
}

.nav-main {
  border-bottom: 1px solid rgba(255, 255, 255, .1);
  margin-bottom: em(20);

  @mixin media $silkDrawerDisappears {
    border-bottom: 0;
    margin-bottom: 0;
  }

  > ul {

    > li {

      @mixin media $silkDrawerDisappears {
        position: relative;
        display: inline-block;
        vertical-align: top;
        padding-top: em(7);
        padding-bottom: em(7);
        margin-right: em(5);
        margin-left: em(5);
        transition: padding .2s;
      }

      @mixin media $yatterman {
        margin-right: em(12);
        margin-left: em(12);
      }

      @mixin media $gamagori {
        margin-right: em(15);
        margin-left: em(15);
      }

      &:nth-child(n+4) {

        > ul {

          @mixin media $silkDrawerDisappears {
            right: 0;
            left: auto;
          }

        }

      }

      &:hover {

        > a {

          @mixin media $silkDrawerDisappears {
            box-shadow: 0 em(6, 14) 0 0 $m3;
          }

        }

        > ul {

          @mixin media $silkDrawerDisappears {
            display: block;
            opacity: 1;
            visibility: visible;
          }

        }

      }

      /*.scrolling & {

        @mixin media $silkDrawerDisappears {
          padding-top: em(5);
          padding-bottom: em(4);
        }

      }*/

      > a {
        @mixin tier-one;

        @mixin media $silkDrawerDisappears {
          padding: em(6, 14) 0;
          color: $m1;
          transition: box-shadow .3s;
        }

        &:hover,
        &.active {

          @mixin media $silkDrawerDisappears {
            box-shadow: 0 em(6, 14) 0 0 $m3;
          }

        }

      }

      ul {
        background: $m1;

        @mixin media $silkDrawerDisappears {
          text-align: left;
          background: #fff;
        }

        li {

          &:last-child {

            a {

              @mixin media $silkDrawerDisappears {
                border-bottom: 0;
              }

            }

          }

        }

          a {
            border-bottom: 1px solid rgba(255, 255, 255, .1);
            padding: em(10, 14);
            padding-left: em(80, 14);
            font-size: em(14);
            color: #fff;

            @mixin media $silkDrawerDisappears {
              border-bottom-color: #979797;
              padding: em(8, 14) em(10, 14);
              color: $m1;
            }

            &:hover {

              @mixin media $silkDrawerDisappears {
                color: $m2;
              }

            }

          }

        > a {
          @mixin tier-one;
          padding-left: em(80, 14);
        }

      }

    }

  }

}

.nav-secondary-main {
  display: none;

  @mixin media $huge {
    display: block;
  }

  ul {
    font-size: em(14);
    letter-spacing: em(.7, 14);
    line-height: nu(22, 14);

    ul {
      display: none;
    }

  }

    li {

      @mixin media $huge {
        display: inline-block;
        vertical-align: top;
        width: 32%;
        margin-bottom: em(8, 18);
      }

      @mixin media $yatterman {
        width: auto;
        margin-right: em(20, 14);
      }

    }

      a {
        display: inline-block;
        vertical-align: top;
      }

}
