/* ========================================
// Swift Slide
// ===================================== */

@define-mixin active-slide {
  position: relative;
  pointer-events: auto;
  opacity: 1;
  visibility: visible;
}

.swift-slide {
  @mixin list-reset;
  position: relative;

  li {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    pointer-events: none;
    opacity: 0;
    visibility: hidden;
    transition:
      opacity .4s,
      visibility .4s,
      transform .4s;

    &.active {
      @mixin active-slide;
    }

  }

}

.product-slide {}

  .product {
    padding: em(75) em(30);

    @mixin media $medium {
      padding-top: em(110);
      padding-bottom: em(110);
    }

    @mixin media $wooser {
      padding-top: em(155);
      padding-bottom: em(155);
    }

    &--interior {

      @mixin media $medium {
        padding-top: em(80);
        padding-bottom: em(80);
      }

      &:before {
        @mixin cover;
        content: '';
        background: rgba(0, 0, 0, .25);
      }

    }

    &:after {
      @mixin clear;
    }

  }

    .product__details {
      float: right;
      max-width: em(600);
      padding: em(90) em(20) em(30);
      background: $m2;
      background: rgba(49, 137, 201, .95);
      color: #fff;

      @mixin media $medium {
        padding: em(110) em(60) em(40);
      }

      &--interior {
        position: relative;
        max-width: em(410);
        padding: em(30);

        @mixin media $medium {
          padding-top: em(40);
        }

      }

      h2 {
        color: inherit;
      }

    }

    @keyframes product {

      100% {
        opacity: 1;
      }

    }

.business-slide {
  overflow: hidden;
  border: 1px solid #E3E3E3;
  margin-bottom: em(50);
  background: #fff;

  .business {
    @mixin active-slide;

    @mixin media $gamagori {
      position: static;
      width: 33.33%;
    }

    &.active {

      @mixin media $gamagori {
        position: static;
      }

    }

    &:last-child {

      .business__label {
        border-bottom: 0;
      }

    }

  }

}

  .business__label {
    display: block;
    border-bottom: 1px solid rgba(227, 227, 227, .4);
    padding: em(14, 16) em(25, 16);
    font-size: em(16);
    font-weight: 700;
    cursor: pointer;
    background: $m1;
    color: #fff;
    transition:
      background .2s,
      box-shadow .2s;

    &:hover {
      background: $m2;
    }

    .active & {
      background: #fff;
      box-shadow: inset em(6) 0 0 0 $m2;
      color: $m1;
    }

  }

  .business__details {
    overflow: hidden;
    max-height: 0;
    padding: 0 em(25);
    background: #fff;
    transition:
      max-height 1s,
      padding 1s,
      opacity .5s,
      visibility .5s;

    @mixin media $gamagori {
      position: absolute;
      top: 0;
      left: 33.33%;
      width: 66.66%;
      padding: 0 em(70);
      opacity: 0;
      visibility: hidden;
    }

    .active & {
      max-height: em(480);
      padding-top: em(30);
      padding-bottom: em(30);

      @mixin media $gamagori {
        padding-top: em(40);
        padding-bottom: em(40);
        opacity: 1;
        visibility: visible;
      }

    }

    .h5 {
      font-size: em(14);
      font-weight: 800;
      text-transform: uppercase;
    }

      a {
        color: $m3;

        &:hover {
          color: $m2;
        }

      }

  }


/* ========================================
// Swift Controls
// ===================================== */

.swift-controls {
  user-select: none;

  .product-slide & {
    display: none;
  }

  .business-slide & {
    background: $m2;

    @mixin media $gamagori {
      width: 33.33%;
    }

    &:after {
      @mixin clear;
    }

  }

  button {
    border: 0;
    padding: 0;
    outline: 0;
    background: none;

    .business-slide & {
      float: left;
      width: 50%;
      padding: em(12);
      text-align: center;
      transition: background .2s;
    }

    /* Previous Button */
    &:first-child {

      .business-slide & {
        box-shadow: inset -1px 0 0 0 rgba(255, 255, 255, .5);
      }

    }

    /* Next Button */
    &:last-child {}

    &:hover {

      .business-slide & {
        background: $m3;
      }

    }

  }

}

  .swift-bubble {
    display: inline-block;
    vertical-align: top;
    transform: rotate(90deg);

    svg {
      display: block;
      height: em(34);
      width: em(34);
      margin: 0 auto;
      fill: #fff;
    }

  }


/* ========================================
// Swift Pager
// ===================================== */

.swift-pager {
  user-select: none;

  .product-slide & {
    position: absolute;
    top: 0;
    left: 50%;
    max-width: em(1310);
    width: 100%;
    padding: em(115) em(30) 0;
    transform: translate(-50%, 0);

    @mixin media $medium {
      padding-top: em(150);
    }

    @mixin media $wooser {
      padding-top: em(195);
    }

  }

  .product-slide.element-halfway & {

    @mixin media $gamagori {
      animation: product .4s forwards;
    }

  }

  .business-slide & {
    display: none;
  }

  span {
    display: inline-block;
    vertical-align: top;
    cursor: pointer;

    .product-slide & {
      position: relative;
      margin-right: rem(10);
      font-family: $sansSerif2;
      font-size: em(20);
      font-weight: 700;
      counter-increment: product-counter;
      color: rgba(255, 255, 255, .3);
      transition: color .2s;

      @mixin media $medium {
        margin-right: rem(20);
        font-size: em(25);
      }

      &:before {
        content: '0' counter(product-counter);
      }

      &:after {
        position: absolute;
        top: 50%;
        left: 50%;
        height: em(40);
        width: em(40);
        content: '';
        background: #fff;
        border-radius: 100%;
        transform:
          translate(-50%, -50%)
          scale(0);
      }

    }

    &:hover {

      .product-slide & {
        color: rgba(255, 255, 255, .75);
      }

    }

    &.active {

      .product-slide & {
        color: #fff;

        &:after {
          animation: burst 1s forwards;
        }

      }

    }

  }

}

@keyframes burst {

  100% {
    opacity: 0;
    transform:
      translate(-50%, -50%)
      scale(1);
  }

}

  .swift-pager-bubble {

    .product-slide & {
      float: right;
      max-width: em(600);
      width: 100%;
      padding: 0 em(20);
      text-align: left;

      @mixin media $medium {
        padding-left: em(60);
      }

    }

  }
