/* ========================================
// Callouts
// ===================================== */

.callout {
  position: relative;
  display: flex;
  align-items: center;
  min-height: em(350);
  padding: em(40) em(30) em(100);
  text-align: center;
  color: #fff;

  @mixin media $gamagori {
    min-height: em(400);
  }

  &:hover {
    color: #fff;
  }

  &--larger {

    @mixin media $aldnoah {
      min-height: em(800);
    }

  }

  &--featured {
    text-align: left;
  }

  &--blue {
    background: $m2;

    &:before {
      @mixin cover;
      content: '';
      background: rgba(40, 119, 177, .8);
    }

  }

  &--orange {
    background: #E88708;

    &:before {
      @mixin cover;
      content: '';
      background: rgba(232, 135, 8, .8);
    }

  }

  &--gray {
    background: #53544F;

    &:before {
      @mixin cover;
      content: '';
      background: rgba(83, 84, 79, .8);
    }
  }

  &--dark-blue {
    background: #0F264B;

    &:before {
      @mixin cover;
      content: '';
      background: rgba(15, 38, 75, .8);
    }
  }

  &--extreme {
    padding: em(20);
    text-align: center;
    background: $m2;

    @mixin media $penguindrum {
      display: flex;
      align-items: center;
      align-content: center;
      justify-content: space-between;
      padding: em(48) em(60) em(47);
    }

    h1 {
      font-size: em(25);
      line-height: nu(36, 25);
      text-transform: none;
      color: #fff;

      @mixin media $penguindrum {
        margin-bottom: 0;
        font-size: em($h1Size);
        line-height: nu($h1Line, $h1Size);
      }

    }

    .button {
      margin-right: 0;

      @mixin media $penguindrum {
        margin-bottom: 0;
      }

    }

  }

  h2,
  h3 {
    color: inherit;
  }

  h2 {
    text-transform: none;

    &:nth-of-type(2) {
      font-weight: normal;
    }

  }

  p {
    margin-bottom: 0;
  }

}

  .callout__details {
    position: relative;
    display: block;
    max-width: em(600);
    margin: 0 auto;
    color: #fff;
    transition: transform .2s;

    &:hover {
      color: #fff;
      transform: scale(1.025);
    }

  }

    .callout__logo {
      margin: em(40) 0 em(28);
    }

  .callout__share {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 30;
    width: 100%;
    padding: em(24) em(30);
    cursor: pointer;
    text-align: right;
    transition:
      background .2s,
      padding .3s;

    .callout--blue & {
      background: $m2;

      &:hover {
        background: #2476b1;
      }

    }

    .callout--orange & {
      background: $m3;

      &:hover {
        background: #d07908;
      }

    }

    .callout--gray & {
      background: #7D8073;

      &:hover {
        background: #66695d;
      }

    }

    .callout--dark-blue & {
      background: $m1;

      &:hover {
        background: #214e94;
      }

    }

    .vue-blog & {
      position: relative;
    }

    &.share-triggered {
      padding-right: em(60);

      li {
        opacity: 1;
        visibility: visible;

        &:nth-child(1) {
          transform:
            translate(-150%, -250%)
            scale(1)
            rotate(0);
        }

        &:nth-child(2) {
          transform:
            translate(-50%, -175%)
            scale(1)
            rotate(0);
          transition-delay: .1s;
        }

        &:nth-child(3) {
          transform:
            translate(0, -50%)
            scale(1)
            rotate(0);
          transition-delay: .2s;
        }

        &:nth-child(4) {
          transform:
            translate(-50%, 75%)
            scale(1)
            rotate(0);
          transition-delay: .3s;
        }

        &:nth-child(5) {
          transform:
            translate(-150%, 150%)
            scale(1)
            rotate(0);
          transition-delay: .4s;
        }

      }

    }

    h3 {
      margin-bottom: 0;
    }

    .nav-social {
      position: absolute;
      top: 50%;
      right: em(50);
    }

      li {
        position: absolute;
        top: 50%;
        left: 50%;
        margin: 0;
        opacity: 0;
        visibility: hidden;
        transform:
          translate(-50%, -50%)
          scale(0)
          rotate(-90deg);
        transition:
          opacity .1s,
          visibility .1s,
          transform .6s;
      }

        a {
          box-shadow: 0 0 em(6) 0 rgba(0, 0, 0, .35);

          &:hover {
            box-shadow: 0 0 em(6) 0 rgba(0, 0, 0, .35);
          }

        }

  }
