/* ========================================
// Silk Harmonica Solutions
// ===================================== */

.solutions {
  position: relative;
  border-bottom: 10px solid $m3;

  &:before {
    position: absolute;
    bottom: -22px;
    left: 50%;
    display: block;
    width: 0;
    height: 0;
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;
    border-top: 20px solid $m3;
    margin-left: -20px;
    content: "";
  }

  .solutions__header {
    padding: em(12) 0;
    border-bottom: 1px solid $s2;

    @mixin media $wooser {
      min-height: 100px;
    }
  }

  .solutions__facet {
    margin-bottom: 0;
    background: #fff;
    text-align: center;
    box-shadow: 0 0 0 1px $s2;

    h6 {
      margin-bottom: em(4, 14);
      color: #7D8073;
      font-size: em(14);
    }

    figure {
      width: 150px;
      height: 150px;
      margin: 0 auto;
      border-radius: 50%;
      overflow: hidden;
    }

      img {
        margin: 0 auto;
      }

  }

  .solutions__image {
    display: none;

    @mixin media $wooser {
      display: block;
      padding: em(24);
    }
  }

  .facetwp-loading {
    margin: 0 auto;
  }

  .facetwp-type-checkboxes,
  .facetwp-type-radio {

    .facetwp-checkbox,
    .facetwp-radio {
      padding: 0;
      margin: 0;
      background: none;

      .facetwp-counter {
        display: none;
      }

      &.checked,
      &.selected {
        color: #464646;
        font-weight: 700;
      }
    }

  }

  .solutions__terms {

    @mixin media $wooser {
      position: relative;
    }

  }

    .solutions__terms--options {
      display: none;
      @mixin h5;
      text-align: center;
      background: #fff;
      font-weight: 400;

      @mixin media $wooser {
        position: absolute;
        left: 0;
        top: 0;
        display: block;
        width: calc(100% - 1px);
        padding: em(12);
        opacity: 0;
        visibility: hidden;
        transition: opacity .5s, top .25s;
        box-shadow: 0 0 0 1px $s2;
      }

      &.active {
        display: block;

        @mixin media $wooser {
          top: em(50);
          opacity: 1;
          visibility: visible;
        }

      }

      .facetwp-facet {
        margin: 0;
      }

      .facetwp-radio {

        &:hover {
          color: $bodyColor;
        }

      }
    }

    .solutions__terms--selected {
      @mixin h5;

      a {
        color: $bodyColor;
      }

      span {
        padding-right: rem(5);
      }

      svg {
        fill: $m2;
      }
    }

}

  .solution__content {
    border: 1px solid $s2;
    padding: em(40) em(30) em(30);
    margin-top: em(35);
    margin-bottom: em(35);
    background: #fff;
    border-radius: 0;

    @mixin media $medium {
      padding-top: em(50);
    }

    @media(min-width: 1200px) {
      padding-right: em(45);
      padding-left: em(45);
    }

  }

  .solution__footer {
    border-top: 1px solid $s2;
    margin: em(40) em(-30) em(-30);
    padding: em(30);

    @media(min-width: 1200px) {
      margin-right: em(-45);
      margin-left: em(-45);
      padding-right: em(45);
      padding-left: em(45);
    }

    h4 {
      position: relative;
      margin-bottom: 0;
      padding-left: em(30, 14);
      font-size: em(14);
      font-weight: 800;
      text-transform: uppercase;
    }

      a {
        display: block;
        color: #7D8073;

        &:hover {
          color: $m2;
        }

      }

      svg {
        position: absolute;
        top: em(4, 14);
        left: 0;
        fill: $m3;
      }

      em {
        font-style: normal;
        font-weight: inherit;
        color: $m3;
      }

  }
