/* ========================================
// Symbols
// ===================================== */

.symbol {
  transition:
    fill .2s,
    stroke .2s;

  &-chevron-left,
  &-chevron-right {
    height: em(26);
    width: em(26);
  }

  &-chevron-down {
    height: em(8);
    width: em(15);
  }

  &-home,
  &-undo,
  &-close {
    height: em(16);
    width: em(16);
  }

  &-search {
    height: em(25);
    width: em(25);
  }

  &-minus,
  &-plus {
    height: em(20);
    width: em(20);
  }

  &-play {
    height: em(65);
    width: em(65);
  }

  &-barcode {
    height: rem(12);
    width: rem(18);
  }

  &-blog {
    height: rem(12);
    width: rem(18);
  }

  &-cart {
    height: rem(14);
    width: rem(18);
  }

  &-plane {
    height: rem(14);
    width: rem(18);
  }

  &-user {
    height: rem(17);
    width: rem(18);
  }

  &-arrow-right,
  &-arrow-left {
    height: em(12);
    width: em(14);
  }

  &-leave {
    height: rem(12);
    width: rem(15);
  }

  &-times {
    height: em(10);
    width: em(10);
  }

  &-pinterest {
    height: em(20);
    width: em(20);
  }

  &-google {
    height: em(18);
    width: em(18);
  }

  &-linkedin {
    height: em(19);
    width: em(19);
  }

  &-facebook {
    height: em(18);
    width: em(18);
  }

  &-instagram {
    height: em(18);
    width: em(18);
  }

  &-twitter {
    height: em(18);
    width: em(18);
  }

  &-youtube {
    height: em(19);
    width: em(19);
  }

}
