/* ========================================
// Buttons
// ===================================== */

[class*='button'] {
  display: inline-block;
  vertical-align: top;
  border: 0;
  margin: em(5) em(10) em(5) 0;
  padding: em(10) em(35);
  cursor: pointer;
  font-family: $sansSerif2;
  font-size: em(16);
  font-weight: 700;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  background: $m3;
  border-radius: em(3, 18);
  color: #fff;
  outline: 0;
  transition:
    background .2s,
    box-shadow .2s,
    color .2s,
    transform .2s;

  @mixin media $big {
    font-size: em(18);
  }

  &:hover {
    background: $m1;
    color: #fff;
  }

  &.full-width {
    width: 100%;

    @mixin media $small {
      width: auto;
    }

  }

}


/* Additional Buttons
// ------------------------------------- */

.button {

  &--inverse {

    &:hover {
      background: $m2;
    }

  }

  &--mini {
    margin-top: 0;
    margin-right: 0;
    margin-bottom: em(15, 14);
    padding: em(4, 14) em(15, 14);
    font-size: em(14);
  }

  &--hollow {
    border: 1px solid;
    background: none;
    color: $m1;

    &:hover {}

  }

  &--hollower {
    border: 0;
    background: none;
    color: $m1;

    &:hover {
      color: $m3;
      background: none;
    }

  }

}

.cta_button { /* Used in hubspot call to action ads */
  background: none;
  border-radius: 0;

  &:hover {
    background: none;
  }
}

.discover {
  margin-bottom: rem(15);
  font-size: em(14);
  font-weight: 700;
  text-transform: uppercase;
  color: #7D8073;

  @mixin media $medium {
    font-size: em(18);
  }

  &:hover {
    color: $m2;
  }

}


/* ========================================
// Site Logo/Link
// ===================================== */

/* If the site logo is text based */

.site__link {
  display: block;
  text-decoration: none;
}

/* If the site logo is image based */
/* Set the max-width to the logo's size */
/* overflow: hidden and white-space: nowrap prevent line breaks */

.site__logo {
  display: block;
  overflow: hidden;
  width: 100%;
  white-space: nowrap;
  background: url('../images/logo.svg') no-repeat 0 50%;
  background-size: contain;
  color: transparent;

  .site__header & {
    height: em(45);
    max-width: em(168);

    @mixin media $silkDrawerDisappears {
      float: left;
      height: em(72);
      max-width: em(268);
      transition:
        height .2s,
        max-width .2s;
    }

    /*.scrolling & {

      @mixin media $silkDrawerDisappears {
        height: em(45);
        max-width: em(168);
      }

    }*/

  }

  .site__footer & {
    height: em(64);
    max-width: em(226);
    margin-right: auto;
    margin-left: auto;
    margin-bottom: em(45);
    background-image: url('../images/logo-white.svg');
    color: transparent;

    @mixin media $medium {
      float: left;
      margin-bottom: 0;
    }

    &:hover {
      box-shadow: none;
    }

  }

  &:hover {
    color: transparent;
  }

}
