/* ========================================
// Hero
// ===================================== */

.hero {
  position: relative;
  background-position: 50% 50% !important;
  background-size: cover !important;

  &--empty {
    height: 45vw;
    min-height: em(210);
    max-height: em(450);
  }

  &--default {
    padding: em(120) em(30) em(20);

    @mixin media $medium {
      padding: em(200) em(60) em(40);
    }

  }

  &--mask {

    &:before {
      @mixin cover;
      content: '';
      background: rgba(0, 0, 0, .35);
    }

  }

}

  .hero__title {
    position: relative;
    margin-bottom: 0;
    color: #fff;
  }
