/* ========================================
// Silk Harmonica
// ===================================== */

/* When do accordions become harmonicas? */
$accordionToHarmonica: 1200px;

@define-mixin expanded-harmonica {

  .silk-harmonica--expanded & {

    @media(min-width: $accordionToHarmonica) {
      @mixin-content;
    }

  }

}

.silk-harmonica {

  &--expanded {

    @media(min-width: $accordionToHarmonica) {
      display: flex;
      flex-wrap: wrap;
    }

  }

}

  .silk-harmonica__header {
    position: relative;
    margin-bottom: em(10);
    padding: em(13) em(30);
    cursor: pointer;
    background: $m2;
    border-radius: em(2);
    color: #fff;
    transition:
      padding .4s,
      background .2s,
      box-shadow .2s,
      color .2s;

    @mixin expanded-harmonica {
      order: 1;
      padding: em(23) em(25);
      background: none;
      border-radius: 0;
      color: #B8B9B1;
    }

    h3 {
      color: inherit;
    }

    &:hover {
      background: $m1;

      @mixin expanded-harmonica {
        background: none;
        box-shadow: inset 0 em(-10) 0 0 $m1;
        color: $m1;
      }

    }

    &.active {

      @mixin expanded-harmonica {
        background: none;
        box-shadow: inset 0 em(-10) 0 0 $m2;
        color: $m2;
      }

      .js & {

        + * {
          display: block;
        }

      }

    }

    & > *:first-child {
      margin-bottom: 0;
    }

  }

    .silk-harmonica__bottle {
      position: absolute;
      top: 0;
      right: 0;
      height: 100%;
      width: em(60);
      background: $m1;
      border-radius: 0 em(2) em(2) 0;

      &:first-of-type {

        .active & {
          display: none;
        }

      }

      &:last-child {
        display: none;

        .active & {
          display: block;
        }

      }

      @mixin expanded-harmonica {
        visibility: hidden;
      }

    }

      .silk-harmonica__bubble {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }

        .silk-harmonica__symbol {
          display: block;
        }

  .silk-harmonica__content {
    border: 1px solid #979797;
    border-top: 0;
    margin-top: em(-10);
    margin-bottom: em(10);
    padding: em(20) em(30);
    background: #fff;
    border-radius: 0 0 em(3) em(3);

    @mixin expanded-harmonica {
      order: 2;
      width: 100%;
      border: 0;
      border-top: 1px solid #979797;
      padding-right: 0;
      padding-left: 0;
      background: none;
    }

    &:after {
      @mixin clear;
    }

    .js & {
      display: none;
    }

  }
