/* ========================================
// Silk Nav
// ===================================== */

.silk-nav {

  ul {

    ul {
      position: absolute;
      top: em(175);
      left: 100%;
      z-index: 300;
      height: 100%;
      width: 100%;
      opacity: 0;
      visibility: hidden;
      transform: translate(0, 0);
      transition:
        opacity .4s,
        visibility .4s,
        transform .4s;

      @mixin media $silkDrawerDisappears {
        top: 100%;
        left: 0;
        height: auto;
        width: em(160);
        transition:
          opacity .2s,
          visibility .2s,
          transform .2s;
      }

      &.silk-nav__nest--active {
        opacity: 1;
        visibility: visible;
        transform: translate(-100%, 0);
        transition:
          opacity .4s,
          visibility .4s,
          transform .4s cubic-bezier(.175, .885, .24, 1.11);

        @mixin media $silkDrawerDisappears {
          transform: none;
        }

      }

      > a {

        @mixin media $silkDrawerDisappears {
          display: none;
        }

      }

    }

  }

}


/* ========================================
// Silk Nav Controls
// ===================================== */

.silk-nav__controls {
  position: relative;
  z-index: 400;
  border-top: 1px solid rgba(255, 255, 255, .1);
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  @mixin media $silkDrawerDisappears {
    display: none;
  }

}

  .silk-nav__trigger {
    border: 0;
    background: none;
    transition:
      background .2s,
      color .2s,
      opacity .2s,
      visibilty .2s;

    &:hover {}

    @mixin media $silkDrawerDisappears {
      display: none;
    }

    &--home,
    &--revert {
      padding: em(13, 14) em(20, 14);
      font-size: em(14);
      font-weight: 700;
      color: #fff;

      &:hover {
        color: #fff;
      }

      svg {
        display: inline-block;
        vertical-align: middle;
        margin-right: em(5, 14);
        fill: #fff;
      }

    }

    &--revert,
    &--reverse {
      opacity: 0;
      visibility: hidden;

      .silk-nav--active & {
        opacity: 1;
        visibility: visible;
      }

    }

    &--revert {
      border-left: 1px solid rgba(255, 255, 255, .1);
    }

    &--reverse,
    &--advance {
      position: absolute;
      padding: em(12) em(17) em(12) em(16);

      svg {
        display: block;
        fill: $m3;
      }

    }

    &--reverse {
      top: 100%;
      left: 0;
      border-right: 1px solid rgba(255, 255, 255, .1);
    }

    &--advance {
      right: 0;
      border-left: 1px solid rgba(255, 255, 255, .1);
      margin-top: em(-50);
    }



  }
