/* ========================================
// Site Header
// ===================================== */

.site__header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 80;
  padding: em(10) em(20);
  background: #fff;

  @mixin media $silkDrawerDisappears {
    padding: 0;
  }

}

  .scarf {
    padding-right: em(54);

    @mixin media $silkDrawerDisappears {
      padding: em(10) em(20);
      transition: padding .2s;
    }

    /*.scrolling & {

      @mixin media $silkDrawerDisappears {
        padding-top: em(5);
        padding-bottom: em(5);
      }

    }*/

  }
